<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor,
    },
    data() {
      return {
        interval: 30,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* 你好啊，小邹
* 本来是给你准备的一个小惊喜
* 但是，还是没忍住;提前告诉你这个是一个什么样子的格式,
* :(
* 在这里,用Windows的蓝屏符号，浅浅的表示一下,我小秘密被识破的难受吧～
* 下面就开始吧...
*/

/* 先给所有元素加上过渡效果 */
* {
  transition: all .1s;
}
/* 设置背景颜色 */
/* 这个颜色是偏黑一点的墨绿色 */
/* 然后字体是稍微温暖点的粉红色,粉嫩粉嫩的~~~ */
html {
  color: rgb(234,233,70); background: rgb(135,48,48);
}
#content{
  height:70vh;
  margin:0;
}
#foot{
  height:29vh;
  margin:0;
}

/* 设置边框 */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  width: 40vw; height: 80vh;
  background: rgb(135,48,48);
}
/* 代码高亮 */
.token.selector{ color: rgb(255,255,255); }
.token.property{ color: rgb(255,153,0); }
.token.punctuation{ color: rgb(255,255,255); }
.token.function{ color: rgb(111,158,232); }

/* 加3D效果 */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 10;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
          transform: rotateY(10deg) translateZ(-100px) ;
}

/* 准备一个编辑器 */
.resumeEditor{
  position: fixed; right: 0; top: 10;
  padding: .5em;  margin: .5em;
  width: 55vw; height: 80vh;
  border: 1px solid;
  background: rgb(208,140,140); color: #faf6eb;
  overflow: auto;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(-10deg) translateZ(-200px) ;
          transform: rotateY(-10deg) translateZ(-200px) ;
}
/* 好啦,格式都设置完了,下面就是... */
/* 小邹,你知道吗,为了能遇见你，我可是透支了之前所有的运气哦~ */
`, `
/*将Markdown格式翻译成HTML
 *再对HTML加点样式
*/`,`
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '•';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #cfe7c5;
}
`],
        currentMarkdown: '',
        fullMarkdown: `
> 伐木丁丁，鸟鸣嘤嘤。出自幽谷，迁于乔木。嘤其鸣矣，求其友声。 相彼鸟矣，犹求友声。矧伊人矣，不求友生？


技能
====
  - 上得了厅堂，下得了厨房
  - 逗小邹开心～
  - 画画
  - 编程

应聘职位
====
  - 小邹同学的私人厨师
  - 小邹同学的私人闹钟
  - 给小邹同学提供独家的叫醒服务
  - 满足小邹同学的所有的要求的服务

要求
====
  - 希望小邹同学能够在心里提供永久的住宿
  
总结:
====
  
  应聘小邹同学的男朋友～～～


如果小邹同学,答应的话,就点下面的东西,嘻嘻嘻嘻,（猜一猜是什么）

* [点这里哦～](http://212.129.243.50)

`
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0);
        await this.progressivelyShowResume();
        await this.progressivelyShowStyle(1);
        await this.showHtml();
        await this.progressivelyShowStyle(2);
      },
      showHtml:function() {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
		  this.$nextTick(() => {
			this.$refs.resumeEditor.goTo()
		})
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) {
              return
            }
            // 计算前 n 个 style 的字符总数
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 1) || ' '
              this.currentStyle += char
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
	min-height: 100vh;position: relative;
  }

  html {
    min-height: 100vh;
  }

  * {
    box-sizing: border-box;
  }
</style>



